import createClient from 'openapi-fetch';
import semver from 'semver';
import * as core from '@actions/core';
import { jwtDecode } from 'jwt-decode';
// eslint-disable-next-line import/no-relative-packages
import pkg from '../../package.json';
export async function getClient(context) {
    const { argusBaseUrl } = context;
    validateCIActionVersion(argusBaseUrl);
    let token = await core.getIDToken();
    const client = createClient({ baseUrl: argusBaseUrl });
    client.use({
        async onRequest(req, _options) {
            if (isExpired(token)) {
                core.info('Token expired, refreshing...');
                token = await core.getIDToken();
            }
            req.headers.set('Authorization', token);
            req.headers.set('User-Agent', getUserAgent(context));
            return req;
        },
    });
    return client;
}
// returns true if the token expires within the threshold
function isExpired(token, thresholdSeconds = 60, decoder = (jwtDecode)) {
    const decoded = decoder(token);
    if (!decoded.exp) {
        throw new Error('Okta token did not have expected exp value');
    }
    const secondsSinceEpoch = Math.round(new Date().getTime() / 1000);
    return (secondsSinceEpoch + thresholdSeconds) > decoded.exp;
}
export function getUserAgent(context) {
    const version = getVersion();
    return `PlatformGithubAction/${version} (${formatContext(context)})`;
}
export function formatContext(context) {
    const resultParts = Object.entries(context).map(([key, value]) => `${key}=${value}`);
    return resultParts.join('; ');
}
export function getVersion() {
    return pkg.version;
}
async function validateCIActionVersion(argusBaseUrl) {
    const resp = await fetch(`${argusBaseUrl}/discovery`);
    const { minimum_ci_action_version: minVersion, current_version: currServerVersion } = await resp.json();
    if (semver.lt(getVersion(), minVersion)) {
        throw new Error(`Minimum CI Action version ${minVersion} required, found ${getVersion()}`);
    }
    if (semver.lt(getVersion(), currServerVersion)) {
        core.warning(`CI Action version ${getVersion()} is behind the current version ${currServerVersion}`);
    }
}
