import { escapeRegExp } from 'lodash';
export function findExistingStackDetails(ctx, prBody) {
    const safeHeader = escapeRegExp(stackDetailsHeader(ctx));
    const safeFooter = escapeRegExp(stackDetailsFooter(ctx));
    const markerRegex = new RegExp(`${safeHeader}[\\s\\S]+${safeFooter}\\s+`);
    const markerMatch = prBody.match(markerRegex);
    if (!markerMatch) {
        return undefined;
    }
    return markerMatch[0];
}
export function getStackDetailsIdentifier({ argusBaseUrl, appName, envName }) {
    return `app=${appName}&env=${envName}&server=${argusBaseUrl}`;
}
export function getStackDetails({ ctx, stackName, stackBaseURL, argocdApplicationURL, grafanaDashboardURL, }) {
    const fullGrafanaDashboardURL = grafanaDashboardURL.startsWith('http') ? grafanaDashboardURL : `https://${grafanaDashboardURL}`;
    const fullBaseURL = stackBaseURL.startsWith('http') ? stackBaseURL : `https://${stackBaseURL}`;
    return `${stackDetailsHeader(ctx)}
<details open>
  <summary>Argus Stack Details</summary>
  <br />
  <table>
    <tr>
      <th>Stack Name</th>
      <th>Env Name</th>
      <th>App Name</th>
    </tr>
    <tr>
      <td>${stackName}</td>
      <td>${ctx.envName}</td>
      <td>${ctx.appName}</td>
    </tr>
  </table>
  <table>
    <tr>
      <th>Stack Base URL</th>
      <td><a href="${fullBaseURL}" target="_blank">${fullBaseURL}</a></td>
    </tr>
    <tr>
      <th>ArgoCD URL</th>
      <td><a href="${argocdApplicationURL}" target="_blank">${argocdApplicationURL}</a></td>
    </tr>
    <tr>
      <th>Dashboard URL</th>
      <td><a href="${fullGrafanaDashboardURL}" target="_blank">${fullGrafanaDashboardURL}</a></td>
    </tr>
  </table>
</details>

---
${stackDetailsFooter(ctx)}
`;
}
export function stackDetailsHeader(ctx) {
    const identifier = getStackDetailsIdentifier(ctx);
    return `<!--ARGUS_STACK_DETAILS_START:${identifier}:do not remove this marker as it will break the argus metadata functionality-->`;
}
export function stackDetailsFooter(ctx) {
    const identifier = getStackDetailsIdentifier(ctx);
    return `<!--ARGUS_STACK_DETAILS_END:${identifier}:do not remove this marker as it will break the argus metadata functionality-->`;
}
